import React from 'react';
import { Switch, Checkbox } from 'evergreen-ui';

function CheckBox(props){
    const [checked, setChecked] = React.useState(props.defaultValue === true)

    if(props.type === 'checkbox'){
        return (
            <>
            <Checkbox
            checked={checked}
            indeterminate={props.indeterminate ? props.indeterminate: false}
            value={props.value} 
            disabled={props.forView} 
            onChange={e => {
                props.onChange(e)
                setChecked(e.target.checked)
            }}
            />

            </>
        )
    }else{
        return (
            <div id="switchDiv">
                {!props.forView && props.label?
                <>
                    <div class="ub-dspl_flex ub-flx-drct_column ub-mb_8px ub-box-szg_border-box"><label class="ub-color_101840 ub-fnt-fam_b77syt ub-f-wght_500 ub-fnt-sze_14px ub-ln-ht_18px ub-ltr-spc_-0-05px ub-dspl_block ub-mb_0px ub-box-szg_border-box" for={props.label}>{props.label}</label></div>
                </>
                :<></>}
                <>
                <Switch
                    name={props.name}
                    id={props.label}
                    label={props.label}
                    checked={checked}
                    onChange={e => {
                        e.target.value = e.target.checked
                        props.onChange(e)
                        setChecked(e.target.checked)
                    }}
                    height={20}
                    disabled={props.forView} 
                    /> 
                </>
                
            </div>
        )
    }
}

export default CheckBox;