import React from "react";


import { useSearchParams } from "react-router-dom"
import ListRecord from "./ListRecord";

function RelatedList(props) {
  const [queryParameters] = useSearchParams()
  const objectId = (queryParameters.get("id"))
  const objectName = props.configurations.o_name

  return (
      <div >
      
        {
          props.configurations.related.map((conf, i)=>(
            <div style={{marginBottom:'20px'}}>
              
              <ListRecord relatedListIndex={i} showTitle={true} showSearch={false} displayNum={ 3 } showNumPerPage={false} relatedFrom={objectName} objectId={objectId} configurations={require('../object-config/'+conf.o_name+'.json')} isrelatedList={true}/>

            </div>
          ))
        }
    </div>
  );
  
}

export default RelatedList;
