import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { get} from "../config/requisitions";

import { v4 as uuid } from 'uuid';
import { Pane, Pagination, Table, Select, Text, SearchInput, Group, IconButton, AddIcon } from 'evergreen-ui'
import UpsertPopUp from './UpsertPopUp';
import BarcodeGenerator from './BarcodeGenerator';
import CheckBox from './CheckBox';


function ListRecord(props){
    const [update, setUpdate] = React.useState(props.update)
    const [isShown, setIsShown] = React.useState(false)
    const objectId = props.objectId

    const relatedFrom = (props.relatedFrom)
    const isrelatedList = (props.isrelatedList != undefined) ? props.isrelatedList : true

    const objectName = props.configurations ? props.configurations.o_name : ''
    const objectPluralLabel = props.configurations ? props.configurations.o_plural_label: ''

    const getAll =  props.configurations.endpoints.get_all
    
    const formatEndpointGetPage = () =>{
        let end = props.configurations.endpoints.get_page.replace('{id}', props.objectId)

        if(isrelatedList || objectId){
            end = props.configurations.endpoints.get_related_page.replace('{id}', objectId).replace('{related_from}', relatedFrom)
        }
        return end
        
    }

    const endpoint_get_page = formatEndpointGetPage() 
    
    const [objectListSelected, setObjectListSelected] = useState([])
    const [objectListShow, setObjectListShow] = useState([])
    const [displayNum, setDisplayNum] = useState(props.displayNum ? props.displayNum: 10)
    const [pageNum, setPageNum] = useState(1)
    const [maxPages, setMaxPages] = useState(1)
    const [numberOfElements, setNumberOfElements] = useState(0)
    const [isAllSelected, setIsAllSelected] = useState(false)
    const showSearch = (props.showSearch != undefined? props.showSearch: true)
    const showNumPerPage = (props.showNumPerPage != undefined? props.showNumPerPage: true)
    
    const relatedListIndex = (props.relatedListIndex != undefined? props.relatedListIndex: 1)
    
    const filterFields = ()=>{
        let list = props.configurations.fields.filter(field => {return field.f_hide_on_list !== true});
        if(isrelatedList){
            list = list.filter(field => {return (field.f_show_on_related === true)})
        }
    
        return list
    }

    const fields = filterFields();

    var pdd = isrelatedList ? '0px': '0px 30px 0px 30px'

    useEffect(() => {        
        updateData(pageNum, displayNum)
    }, [props.update || update]);

    const numOp = [
        5,
        10,
        15,
        20,
        50,
        100
    ]

    const renderField = (element, field)=>{
        
        if(field.f_main === true){
            return (
                <>
                    <Link class="link-opacity-100" to={"/"+objectName+"/view?id="+element.id} >
                        {formatValue(element[field.f_name], field)}

                    </Link>
                </>
            )
        }else if(element[field.f_name] instanceof Object){
            return <>
                <Link class="link-opacity-100" to={"/"+field.f_name+"/view?id="+element[field.f_name].id} >
                    {formatValue(element[field.f_name], field)}

                </Link>
            </>
        }

        return <>{formatValue(element[field.f_name], field)}</>
    }

    return (
        <div style={{padding: pdd}}>
            {
                isrelatedList ?
                <>
                    <Pane style={{backgroundColor:"#e6e6eb", height:"30px", display:"flex", alignItems:"center", justifyContent:'space-between'}} elevation={1}>
                        <div style={{marginLeft:'50px', display:"flex", alignItems:"center"}}>
                            <Text size={500}>Relacionados - {objectPluralLabel} ({numberOfElements})</Text>

                            
                        </div>
                        <div style={{float: 'right'}}>  
                            <Group  size="medium">
                                <IconButton  icon={AddIcon}  appearance="minimal"  marginRight={10} onClick={()=>{setIsShown(true)}}/>
                            </Group>
                        </div>  
                    </Pane>
                    <br></br>
                </>
                :<></>

            }
            <div style={{display: 'flex', justifyContent:'flex-end', alignItems:'center'}}>
                
                {showSearch ?
                    <div>
                        
                        <span style={{fontSize:12, color:'gray'}}>Total: {numberOfElements}</span>
                        &nbsp;
                        &nbsp;
                        <SearchInput onChange={(e) => {
                            let val = e.target.value

                            if(!val || objectListShow.length === 0){
                                updateData(pageNum, displayNum)
                            }else{
                                setObjectListShow(filterListByName(objectListShow, val))
                            }
                            
                        }}/>
                    </div>
                    
                :<></>
                }   
                {showNumPerPage ? 
                <div className="show" style={{padding: '10px', paddingRight:'30px'}}>
                    <Text>Exibir:</Text>
                    &nbsp;
                    <Select 
                            value={displayNum}
                            onChange={(e) => {
                            let op = e.target.value
                            updateData(pageNum, op);
                            setDisplayNum(op)
                        }}>
                        {
                            numOp.map(op=>(
                                <option value={op}>{op}</option>
                            ))
                        }
                    </Select>
                </div>
                :<></>
                }
            </div>
            
            <Table key={()=>{uuid()}}>
                <Table.Head>
                    {/* {
                        !isrelatedList ? 
                            <Table.TextHeaderCell maxWidth={50}>
                                <CheckBox 
                                type="checkbox"
                                indeterminate={
                                    objectListSelected.length > 0
                                }
                                defaultValue={isAllSelected && objectListSelected.length >= displayNum}
                                onChange={e=>{
                                    setIsAllSelected(e.target.checked);
                                    let list = []
                                    objectListShow.forEach(element => {
                                        list.push(element.id.toString())
                                    });
                                    console.log('List ', list)
                                    setObjectListSelected(list)
                                }}
                                />
                            </Table.TextHeaderCell>
                        :
                        <></>
                    } */}
                    {fields.map(field=>(
                        <Table.TextHeaderCell >{field.f_label}</Table.TextHeaderCell>
                    ))}
                </Table.Head>

                <Table.Body >
                    {
                        objectListShow.map((element)=>(
                            <>
                            <Table.Row key={element.id} isSelectable={false} height={40}>
                                {/* {
                                    !isrelatedList ? 
                                        <Table.Cell maxWidth={50}>
                                            <CheckBox 
                                            type="checkbox"
                                            value={element.id} 
                                            defaultValue={
                                                objectListSelected.some(e=>{ return e == element.id})
                                            }
                                            onChange={e=>{  
                                                    let list = objectListSelected;

                                                    if(e.target.checked){
                                                        list.push(e.target.value)
                                                        list.filter((item, index, arr) => {
                                                            return arr.indexOf(item) === index;
                                                          })               
                                                        setObjectListSelected(list)

                                                        setIsAllSelected(list.length >= numberOfElements)
                                                    }else{
                                                        list = list.filter(el=>{return el != e.target.value})
                                                        setObjectListSelected(list)
                                                    }

                                                    if(props.getSelected){
                                                        props.getSelected(list)
                                                    }

                                                    console.log('List ', list)
                                                }}/>

                                        </Table.Cell>
                                    :
                                    <></>
                                } */}
                                

                                {fields.map(field=>(
                                    <Table.TextCell>{
                                        renderField(element, field)
                                    }</Table.TextCell>     
                                ))}
                            </Table.Row>

                            </>
                        ))
                    }
                </Table.Body  >

            </Table>
            <br></br>
            <div style={{display: 'flex', flexDirection:'column', alignItems:'center', justifyContent: 'center'}}>
                {!isrelatedList ? 
                    <>
                        <br></br>
                        <Text size={13}>Pginas</Text>

                        <Pagination page={pageNum} totalPages={maxPages} onNextPage={()=>{
                            addPage()
                        }}
                        onPreviousPage={()=>{
                            removePage()
                        }}
                        onPageChange={(page)=>{
                            setPage(page)
                        }}
                        ></Pagination>
                    </>
                :
                    <>
                        <Link class="link-opacity-100" to={"/"+relatedFrom+"/"+objectName+"/list/view?id="+objectId+"&rli="+relatedListIndex} >Visualizar tudo</Link>
                    </>
                }
            </div>
            <UpsertPopUp whenDone={()=>{
                setUpdate(update+1)
            }}existsObject={{[relatedFrom]:{id:objectId}}} isShown={isShown} setIsShown={setIsShown} configurations={props.configurations}/>

        </div>
    )
    
    function removePage(){
        let newPnum = pageNum-1 
        if(newPnum === 0) return;
        setPageNum(newPnum)
        updateData(newPnum, displayNum)
    }
    
    function setPage(pnum){
        let newPnum = pnum

        if(newPnum === 0) return;

        setPageNum(newPnum)

        updateData(newPnum, displayNum)   
    }

    function addPage(){
        let newPnum = pageNum+1
        if(maxPages < newPnum) return;

        setPageNum(newPnum)
        updateData(newPnum, displayNum)


    }

    function filterListByName(list, searchString) {
        const normalizedSearch = searchString
          .toLocaleLowerCase()
          .normalize('NFD')
          .replace(/[\u0300-\u036f]/g, '');
      
        return list.filter(item => {
          const normalizedItemName = item.name
            .toLocaleLowerCase()
            .normalize('NFD')
            .replace(/[\u0300-\u036f]/g, '');
      
          return normalizedItemName.includes(normalizedSearch);
        });
      }

    function updateData(page, display, up){
        get(endpoint_get_page+'?size='+display+'&page='+(page-1)).then(result=>{
            setMaxPages(result.totalPages)
            setNumberOfElements(result.totalElements)
            
            setObjectListShow(result.content)
        }).catch(error=>[
            
        ])

    }

    function formatMoney(val){
        let money;

        money = val.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' });

        return money;
    }

    function formatDate(dateString){
        const [year, month, day] = dateString.split('-');

        // Format the date to Brazilian locale date format (dd/mm/yyyy)
        const formattedDate = `${day}/${month}/${year}`;

        return formattedDate;
    }

    function formatValue(value, f){
        let val=value;
        switch(f.f_format){
            case "date":
                val = formatDate(value)

                break;

            case "datetime":
                val = val;

                break;

            case "user":
                val = value.name

                break;
            case "lookup":
                val = value ? value.name :''

                break;
            case "money":
                val = formatMoney(value)

                break;
            case "check":
                val = (
                    <CheckBox name={f.f_name} type="checkbox" label={f.f_label} defaultValue={value}
                    forView={true} />
                )

                break;
            case "barcode":
                val = (<>
                        <BarcodeGenerator 
                            barcodeValue={value}
                            displayValue={false}
                            margin="10"
                            height="30"
                            width="1"
                            />
                    </>)
                break;

        } 
        
        return val;
    }
}

export default ListRecord;