import React, { useRef, useEffect } from 'react';
import JsBarcode from 'jsbarcode';

const BarcodeGenerator = (props) => {
  const canvasRef = useRef(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    // const context = canvas.getContext('2d');
    // context.clearRect(0, 0, canvas.width, canvas.height);
    const options = {
      format: props.format || (isValidEAN(props.barcodeValue) ? 'EAN13':'CODE128'),
      displayValue: props.displayValue, // Hide the text value below the barcode
      width: props.width,
      height: props.height,
      font:'monospace', 
      fontSize: 16, 
      margin: props.margin | 0,
      background: '#ffffff',
      lineColor: '#000000',
    };

    JsBarcode(canvas, props.barcodeValue, options);
  }, [props.barcodeValue]);

  return (
    <img
      ref={canvasRef}
    ></img>
  );

  function isValidEAN(code) {
    // Remove any non-digit characters from the input code
    code = code.toString().replace(/\D/g, '');
  
    // Check if the code is either EAN-8 or EAN-13
    if (code.length !== 8 && code.length !== 13) {
      return false;
    }
  
    // Calculate the checksum digit
    const checkSumDigit = Number(code[code.length - 1]);
    const codeWithoutCheckDigit = code.slice(0, -1);
  
    let sum = 0;
    for (let i = codeWithoutCheckDigit.length - 1; i >= 0; i--) {
      const digit = Number(codeWithoutCheckDigit[i]);
      sum += i % 2 === 0 ? digit : digit * 3;
    }
  
    const calculatedCheckDigit = (10 - (sum % 10)) % 10;
  
    // Compare the calculated checksum digit with the provided checksum digit
    return calculatedCheckDigit === checkSumDigit;
  }
};

export default BarcodeGenerator;