import React from "react";
import { del } from "../config/requisitions";

import BasePage from "./BasePage";
import ListRecord from "../components/ListRecord";
import UpsertPopUp from "../components/UpsertPopUp";

import { Icon, Group, Button, InheritedGroupIcon, AddIcon, TrashIcon } from "evergreen-ui";

function Profile(props) {
  const [isShown, setIsShown] = React.useState(false)
  const [selectedList, setSelectedList] = React.useState([])
  const [update, setUpdate] = React.useState(1)

  return (
    <BasePage
      title={
        <>
          <Icon icon={InheritedGroupIcon} size={25}></Icon>
          &nbsp;
          {props.configurations.o_plural_label}
        </>
      }
      buttons={
        <Group size="medium">
          <Button
            appearance="primary"
            onClick={() => {
              setIsShown(true);
            }}
          >
            Novo &nbsp;
            <Icon icon={AddIcon}></Icon>
          </Button>
          <Button
            appearance="primary"
            intent="danger"
            onClick={() => {
              console.log('List ', selectedList)
              // selectedList.forEach(element => {
              //   console.log('Delete ',   element)
              //   let end = props.configurations.endpoints.delete.replace('{id}', element)
              //   del(end);
              // });
              // setUpdate(update+1)
            }}
          >
            <Icon icon={TrashIcon}></Icon>
            &nbsp;
            Remover 
          </Button>
        </Group>
      }
    >
      <ListRecord showTitle={true} configurations={props.configurations} update={update} isrelatedList={false} getSelected={e=>{
          
          setSelectedList(e)
      }}/>

      <UpsertPopUp
        whenDone={()=>{
          setUpdate(update+1)
        }}
        isShown={isShown}
        setIsShown={setIsShown}
        configurations={props.configurations}
      />
    </BasePage>
  );
}

export default Profile;
